import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {useStaticQuery, graphql} from "gatsby"
import Img from "gatsby-image"
import foto1 from "../images/convert-audio-mac.png"
import PostIndex2 from "../components/postIndex2/postIndex2";


const vitmainaC = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Vitamina C"
                meta="Toda la información que necesitas sobre la vitamina C. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina C?"
                description="Toda la información que necesitas sobre la vitamina C. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina C?"
                lang="es"
            />

            <h1>¿Qué es la vitamina C?</h1>
            <PostIndex2 linksData={[
                {"url": "#funciones", "text": "Funciones"},
                {"url": "#deficiencia", "text": "Deficiencia"},
                {"url": "#alimentos", "text": "Alimentos"},
            ]}
            />
            <hr/>
            <p class="introductionText">La vitamina C, tambi&eacute;n conocida como &aacute;cido L-asc&oacute;rbico, es
                una vitamina soluble en
                agua. A diferencia de la mayor&iacute;a de los mam&iacute;feros y otros animales, los humanos no tienen
                la capacidad de sintetizar la vitamina C y deben obtenerla a trav&eacute;s de la dieta.&nbsp;</p>
            <hr/>
            <p></p>
            <h2 name={"funciones"}>Para que sirve la vitamina C</h2>
            <p><b>La vitamina C juega un papel importante en un n&uacute;mero de funciones corporales incluyendo la
                producci&oacute;n de col&aacute;geno, L-carnitina y algunos neurotransmisores. Ayuda a metabolizar las
                prote&iacute;nas y su actividad antioxidante puede reducir el riesgo de algunos c&aacute;nceres.</b></p>
            <p>Estudios indican que un mayor nivel de vitamina C, evaluado mediante la medici&oacute;n de la vitamina C
                en circulaci&oacute;n, se asocia con menores riesgos de hipertensi&oacute;n, enfermedad coronaria y
                accidente cerebrovascular.</p>
            <p>El col&aacute;geno, que la vitamina C ayuda a producir, es el principal componente del tejido conectivo y
                la prote&iacute;na m&aacute;s abundante en los mam&iacute;feros. Entre el 1 y el 2% del tejido muscular
                es col&aacute;geno. Es un componente vital en los tejidos fibrosos como:</p>
            <ul>


                <li>Tendones</li>
                <li>Ligamentos</li>
                <li>Piel</li>
                <li>C&oacute;rnea</li>
                <li>Cart&iacute;lago</li>
                <li>Huesos</li>
                <li>El intestino</li>
                <li>Los vasos sangu&iacute;neos</li>
            </ul>
            <p></p>
            <p></p>
            <h3>Otros beneficios</h3>
            <p>Otros beneficios de la vitamina C pueden ser los siguientes:</p>

            <p><b>Salud cardiovascular:</b> La vitamina C puede ensanchar los vasos sanguíneos, lo que podría ayudar a
                proteger contra las enfermedades cardíacas y la hipertensión, o la presión arterial alta.</p>
            <p><b>Niveles de colesterol:</b> Se encontró que estos son más bajos en los individuos con niveles adecuados
                de vitamina C.</p>
            <p><b>Cataratas:</b> La vitamina C puede ayudar a reducir el riesgo de cataratas así como de degeneración
                macular relacionada con la edad.</p>
            <p><b>La diabetes:</b> Es menos probable que los pacientes experimenten un deterioro de los riñones, los
                ojos y los nervios si comen muchas frutas y verduras ricas en vitamina C.</p>
            <p><b>Anemia:</b> La vitamina C mejora la absorción del hierro.</p>

            <p><b>Histamina:</b> La histamina es una sustancia que produce el sistema inmunológico, lo que provoca
                inflamación y otros problemas. Un estudio de 1992 encontró niveles más bajos de histamina en la sangre
                en personas que tomaron 2 gramos de vitamina C por día.</p>

            <h2 name={"deficiencia"}>Deficiencia de vitamina C</h2>
            <p></p>
            <p><b>El escorbuto es el resultado de la deficiencia de vitamina C.</b> Sus s&iacute;ntomas incluyen
                articulaciones
                hinchadas, enc&iacute;as sangrantes y dientes flojos, anemia y cansancio.</p>

            <p>En los adultos, los síntomas de deficiencia de vitamina C se desarrollan después de semanas o meses de
                agotamiento de la vitamina C. La lasitud, la debilidad, la irritabilidad, la pérdida de peso y las
                mialgias y artralgias vagas pueden desarrollarse tempranamente.</p>

            <p>Los síntomas de escorbuto (relacionados con defectos en los tejidos conjuntivos) se desarrollan después
                de unos pocos meses de deficiencia. Puede desarrollarse hiperqueratosis folicular, cabello en espiral y
                hemorragias periféricas. <b>Las encías pueden hincharse, ponerse moradas, esponjosas y quebradizas, sangran
                fácilmente en caso de deficiencia grave.</b> Con el tiempo, los dientes se aflojan y se avivan. Pueden
                desarrollarse infecciones secundarias. <b>Las heridas se curan mal y se desgarran fácilmente, y pueden
                producirse hemorragias espontáneas,</b> especialmente como equimosis en la piel de los miembros inferiores o
                como hemorragia de la conjuntiva bulbar.</p>

            <p></p>
            <h2 name={"alimentos"}>Alimentos con vitamina C</h2>
            <ul style={{listStyleType: `decimal`}}>
                <li> Guava</li>
                <li>Pimiento rojo dulce</li>
                <li>Jugo de tomate</li>
                <li> Zumo de naranja</li>
                <li> Pimiento verde dulce</li>
                <li> Pimiento verde picante</li>
                <li> Naranjas</li>
                <li> Fresas</li>
                <li> Papaya</li>
                <li> Zumo de pomelo rosa</li>

            </ul>
        </Layout>
    )
}

export default vitmainaC
